body {
  color: #111;
  background-color: #f6f6f6;
}

* {
  margin: 0;
  padding: 0;
  font-family: DM Sans, sans-serif;
}

ul {
  list-style: none;
}

svg {
  display: block;
}

img {
  object-fit: cover;
  max-width: 100%;
  height: 100%;
  display: block;
}

a {
  cursor: pointer;
  text-decoration: none;
}

.container {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

.title {
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 120px;
  padding-left: 20px;
  display: flex;
}

.title-shopping {
  letter-spacing: -.04em;
  width: 193px;
  height: 38px;
  font-family: DM Sans;
  font-size: 32px;
  font-weight: 700;
  line-height: 38px;
}

.title-list {
  color: #4f2ee8;
}

@media screen and (width >= 768px) {
  .title {
    padding-top: 40px;
    padding-bottom: 140px;
    padding-left: 32px;
  }

  .title-shopping {
    color: #111;
    width: 292px;
    height: 52px;
    font-size: 48px;
    line-height: 52px;
  }
}

.empty-list {
  grid-row: inherit;
  text-align: center;
  gap: 14px;
  width: 265px;
  height: 248px;
  padding-left: 55px;
  display: grid;
}

.empty-list__text {
  letter-spacing: -.02em;
  text-align: center;
  width: 244px;
  height: 36px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.empty-list__image {
  width: 265px;
  height: 198px;
  display: flex;
}

@media screen and (width >= 768px) {
  .empty-list {
    gap: 20px;
    width: 349px;
    height: 309px;
    padding-left: 210px;
  }

  .empty-list__text {
    width: 349px;
    height: 48px;
    font-size: 18px;
    line-height: 24px;
  }

  .empty-list__image {
    width: 322px;
    height: 241px;
  }
}

@media screen and (width >= 1440px) {
  .empty-list {
    padding-left: 355px;
  }
}

